.home-upcoming {
  @include tablet {
    // min-height: 420px;
    padding-bottom: 17px;
  }

  @include desktop {
    // min-height: 660px;
    padding-bottom: 40px;
  }
}

.one-title {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.18;
  text-transform: uppercase;
  color: $light-color;
  margin-bottom: 24px;

  @include tablet {
    font-size: 24px;
    line-height: 1.16;
  }

  @include desktop {
    font-size: 28px;
    line-height: 1.17;
  }
}

.movieDescription {
  @include desktop {
    display: flex;
    justify-content: space-between;
    gap: 16px;
  }
}

.image-upcoming {
  margin-bottom: 24px;

  @include tablet {
    margin-bottom: 32px;
  }

  @include desktop {
    margin-bottom: 0px;
  }
}

.gallery-item_img {
  width: 100%;
  border-radius: 5px;
  object-fit: cover;

  @include tablet {
    width: 704px;
    max-height: 400px;
  }

  @include desktop {
    width: 805px;
    max-height: 458px;
  }
}

.gallery-item {
  @include desktop {
    max-width: 390px;
    max-height: auto;
  }
}

.info-item-title {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.15;
  color: $light-color;
  margin-bottom: 20px;

  @include tablet {
    font-size: 28px;
    line-height: 1.17;
  }

  @include desktop {
    font-size: 32px;
    line-height: 1.18;
    margin-bottom: 28px;
  }
}

.info {
  margin-bottom: 8px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: row;
}

div .info-item-one-part,
div .info-item-two-part {
  flex-basis: calc((100% - 50px) / 2);

  @include tablet {
    flex-basis: calc((100% - 50px) / 4);
  }

  @include desktop {
    flex-basis: calc((100% - 50px) / 2);
  }
}

div .info-item,
div .info-item-two {
  margin-bottom: 12px;
}

.info-item {
  color: $color-subtitles;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.18;
  display: flex;
  align-items: center;
}

.info-item-two-part {
  display: flex;
  flex-direction: column;
}

.info-item-two {
  color: $light-color;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.18;
}

.info-item-fourth {
  background: $light-color;
  border-radius: 5px;
  padding: 1px 8px;
  max-width: 36px;
  max-height: 18px;
}

.vote-text {
  font-size: 14px;
  line-height: 1.14;
  text-transform: uppercase;
  color: $dark-color;
}

.info-item-second {
  color: $accent-color;
  text-transform: uppercase;
}

.info-item-about {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.info-item-thirty {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.18;
  text-transform: uppercase;
  color: $color-subtitles;
}

.info-item-about-movie {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: $text-color;
}
